idth)), 40vw)\n  );\n\n  --game-title-font-size: calc(\n    0.35 * min(calc(0.4 * var(--max-window-width)), 40vw)\n  );\n\n  /* can't divide 30px / 480px sadly so this is an independent calc*/\n  --player-window-title-font-scalar: calc(30 / 480);\n  --player-normal-title-font-scalar: calc(20 / 480);\n\n  --window-title-font-size: calc(\n    var(--player-window-scalar) * var(--player-window-title-font-scalar)\n  );\n\n  --window-normal-font-size: calc(\n    var(--player-window-scalar) * var(--player-normal-title-font-scalar)\n  );\n\n  --player-window-width: var(--player-window-scalar);\n  --player-window-height: calc(1.3 * var(--player-window-scalar));\n\n  /* control board-grid size against player-window */\n  --grid-dimension: calc(0.1 * var(--player-window-width));\n\n  --lightFontColor: rgb(182, 207, 215);\n  --overlayColour: rgba(0, 8, 8, 0.7);\n\n  --mainFontColor: rgb(145, 141, 217);\n  --gameAreaColour: rgb(29, 63, 8);\n}\n\n@font-face {\n  font-family: 'armalite';\n  src: url('./assets/armalite-rifle/armalite.ttf');\n  font-weight: 600;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'gunplay';\n  src: url('./assets/gunplay/gunplay rg.otf');\n  font-weight: 600;\n  font-style: normal;\n}\n\nbody {\n  background-color: rgb(21, 49, 68);\n  margin: 0;\n  display: grid;\n  /* justify-content: stretch; */\n  place-items: center;\n}\n\n#main-container {\n  background-color: rgb(21, 49, 68);\n  background-image: url('./assets/background.jpg');\n  background-size: cover;\n  background-position: center;\n  background-repeat: no-repeat;\n\n  max-width: var(--max-window-width);\n  display: grid;\n  margin: 0;\n  justify-items: stretch;\n  row-gap: 20px;\n  column-gap: 20px;\n  grid-template-columns: repeat(2, 1fr);\n}\n\n#topbar {\n  /* background-color: var(--overlayColour); */\n  padding: 10px;\n  grid-row: 1;\n  grid-column: 1/3;\n  /* font-size aims to be 90% of active window at all times */\n  font-size: var(--game-title-font-size);\n  font-family: 'armalite';\n  display: grid;\n  place-items: center;\n  animation: 2s oscillate infinite alternate;\n}\n\n@keyframes oscillate {\n  0% {\n    color: black;\n  }\n\n  100% {\n    color: rgb(0, 0, 255);\n  }\n}\n\n#messages {\n  margin: 0 20px;\n  background-color: var(--overlayColour);\n  border-radius: 20px;\n  padding: 30px;\n  grid-row: 2;\n  grid-column: 1/3;\n  display: grid;\n  place-items: center;\n  text-align: center;\n  color: var(--lightFontColor);\n  font-size: var(--window-title-font-size);\n  font-weight: 800;\n  font-family: 'armalite';\n}\n\n/* player-window is the window inside which different containers will be inserted - the board or ship selection */\n.player-window {\n  background-color: var(--overlayColour);\n  color: var(--lightFontColor);\n  border-radius: 30px;\n  grid-row: 3;\n  justify-self: center;\n  height: var(--player-window-height);\n  width: var(--player-window-width);\n  display: grid;\n  font-family: 'armalite';\n}\n\n#player-1-window {\n  grid-column: 1;\n}\n\n#computer-window {\n  grid-column: 2;\n}\n\n.player-gameboard {\n  display: grid;\n  /* justify-content: stretch;\n  align-content: stretch; */\n  place-items: center;\n  /* ratio gives a square board area with 10/13 of height */\n  grid-template-rows: 1fr 2fr 10fr;\n}\n\n.crosshairs:hover {\n  cursor: crosshair;\n}\n\n.player-gameboard-title {\n  /* font-size: calc(\n    var(--player-window-scalar) * var(--player-window-title-font-scalar)\n  ); */\n  font-size: var(--window-title-font-size);\n  font-weight: 800;\n}\n\n.player-gameboard-text {\n  /* font-size: calc(\n    var(--player-window-scalar) * var(--player-normal-title-font-scalar)\n  ); */\n  padding: 0 30px;\n  font-size: var(--window-normal-font-size);\n  text-align: center;\n}\n\n.player-gameboard-board {\n  font-size: calc(\n    var(--player-window-scalar) * var(--player-normal-title-font-scalar)\n  );\n\n  display: grid;\n}\n\n.board-cell {\n  border: 0.2px solid rgb(8, 77, 161);\n  /* background-color: rgb(159, 177, 200); */\n  background-color: var(--lightFontColor);\n  /* width: var(--grid-dimension);\n  height: var(--grid-dimension); */\n  display: grid;\n  place-items: center;\n}\n\n.with-ship {\n  /* background-color: black; */\n  background-image: url('./assets/ship.svg');\n  background-size: calc(0.7 * var(--grid-dimension));\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.no-ship {\n  /* background-image: url('./assets/wave.svg'); */\n  /* background-size: calc(0.7 * var(--grid-dimension)) var(--grid-dimension);\n  background-repeat: no-repeat;\n  background-position: center; */\n}\n\n.miss {\n  background-image: url('./assets/dot.svg');\n  background-size: calc(1 * var(--grid-dimension));\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.hit {\n  /* background-color: orange; */\n  background-image: url('./assets/boom-hit.svg');\n  background-size: calc(0.8 * var(--grid-dimension));\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.sunk {\n  /* background-color: darkred; */\n  background-image: url('./assets/boom-sunk.svg');\n  background-size: var(--grid-dimension);\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.ship-board {\n  display: grid; /* can be overwritten with display: none */\n\n  /* justify-content: stretch; */\n  /* align-content: stretch; */\n\n  /* place-items: center; */\n  /* ratio gives room for 5x5 size with 7/13 of height, REVISIT */\n  grid-template-rows: 1fr 1fr 4fr 7fr;\n}\n\n.ship-board-title {\n  display: grid;\n  place-items: center;\n  font-size: calc(\n    var(--player-window-scalar) * var(--player-window-title-font-scalar)\n  );\n  font-weight: 800;\n}\n\n.ship-board-text {\n  display: grid;\n  place-items: center;\n  font-size: calc(\n    var(--player-window-scalar) * var(--player-normal-title-font-scalar)\n  );\n}\n\n.ship-board-selector {\n  border: 2px double black;\n  margin: calc(var(--player-window-width) / 48)\n    calc(var(--player-window-width) / 6);\n  /* padding: 0 80px; */\n  display: grid;\n  row-gap: 5px;\n  place-items: center;\n\n  /* justify-content: stretch;\n  align-content: stretch; */\n  grid-template-rows: repeat(3, 1fr);\n  grid-template-columns: repeat(7, 1fr);\n}\n\n#carrier-img-selector {\n  width: calc(\n    5 * var(--grid-dimension) - 10px\n  ); /* scaling length minus padding */\n  height: calc(0.9 * var(--grid-dimension));\n  grid-row: 1;\n  grid-column: 2/7;\n}\n\n#battleship-img-selector {\n  width: calc(\n    4 * var(--grid-dimension) - 10px\n  ); /* scaling length minus padding */\n  height: calc(0.9 * var(--grid-dimension));\n  grid-row: 3;\n  grid-column: 1/5;\n}\n\n#destroyer-img-selector {\n  width: calc(\n    3 * var(--grid-dimension) - 10px\n  ); /* scaling length minus padding */\n\n  height: calc(0.9 * var(--grid-dimension));\n  grid-row: 2;\n  grid-column: 1/4;\n}\n\n#submarine-img-selector {\n  width: calc(\n    3 * var(--grid-dimension) - 10px\n  ); /* scaling length minus padding */\n\n  height: calc(0.9 * var(--grid-dimension));\n  grid-row: 2;\n  grid-column: 5/8;\n}\n\n#cruiser-img-selector {\n  width: calc(\n    2 * var(--grid-dimension) - 10px\n  ); /* scaling length minus padding */\n\n  height: calc(0.9 * var(--grid-dimension));\n  grid-row: 3;\n  grid-column: 6/8;\n}\n\n.ship-board-viewer {\n  display: grid;\n  place-items: center;\n}\n\n/* effectively a 5x5 size grid inside the viewer */\n#ship-board-viewer-box {\n  border: 1px solid black;\n  width: calc(5.5 * var(--grid-dimension));\n  height: calc(5.5 * var(--grid-dimension));\n  display: grid;\n  place-items: center;\n}\n\n#draggable-container {\n  display: grid;\n}\n\n#options {\n  /* background-color: var(--overlayColour);\n  border-radius: 20px; */\n  /* margin: 20px; */\n  padding: 20px;\n  grid-row: 4;\n  grid-column: 1/3;\n  display: grid;\n  place-items: center;\n}\n\n#new-game-button {\n  background-color: black;\n  font-size: var(--window-title-font-size);\n  padding: 10px 20px;\n  font-family: 'armalite';\n  color: var(--lightFontColor);\n}\n\n/* tablet size, player windows above each other */\n@media (width > 640px) and (width <= 1024px) {\n  body {\n    /* background-color: green; */\n  }\n\n  #main-container {\n    grid-template-rows: repeat (4, auto);\n    grid-template-columns: repeat(1, 1fr);\n  }\n\n  #topbar {\n    grid-row: 1;\n    grid-column: 1;\n  }\n\n  #messages {\n    grid-row: 2;\n    grid-column: 1;\n  }\n\n  #player-1-window {\n    grid-row: 3;\n    grid-column: 1;\n  }\n\n  #computer-window {\n    grid-row: 4;\n    grid-column: 1;\n  }\n\n  #options {\n    grid-row: 5;\n    grid-column: 1;\n  }\n}\n\n/* mobile, columns, player windows filling full width */\n@media (width <= 640px) {\n  body {\n    /* background-color: red; */\n    background-image: none;\n  }\n\n  #main-container {\n    grid-template-rows: repeat (4, auto);\n    grid-template-columns: repeat(1, 1fr);\n  }\n\n  #topbar {\n    grid-row: 1;\n    grid-column: 1;\n  }\n\n  #messages {\n    grid-row: 2;\n    grid-column: 1;\n  }\n\n  #player-1-window {\n    grid-row: 3;\n    grid-column: 1;\n  }\n\n  #computer-window {\n    grid-row: 4;\n    grid-column: 1;\n  }\n\n  #options {\n    grid-row: 5;\n    grid-column: 1;\n  }\n}\n\n/* for non-mobile screens, centre vertically when high window */\n/* NEED TO CALCULATE HEIGHT FOR APPROPRIATE MARGIN! */\n@media (height >= 1300px) and (width <= 640px) {\n  body {\n    margin: 5vh 0;\n  }\n}\n\n/* DISPLAY CLASSES MUST BE PLACED LAST TO OVERRIDE OTHER CLASSES WHEN NECESSARY */\n.display-grid {\n  display: grid;\n}\n.display-none {\n  display: none;\n}\n/* \n#temp-drag-drop {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  place-items: center;\n}\n\n#drop-zone {\n  background-color: black;\n  width: 30vw;\n} */\n"],"sourceRoot":""}]);
